"use client"

import { useState, useEffect, useMemo } from "react"
import { useSearchParams } from "next/navigation"
import { ChevronLeft, ChevronRight, Clock, MapPin, User, Filter, Edit, Trash2, Upload, Plus, X, Calendar as CalendarIcon, AlertTriangle } from "lucide-react"
import { Calendar } from "@/components/ui/calendar"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { lichDayData as initialLichDayData, giangVienData, classesData } from "@/lib/data"
import { checkScheduleConflict } from "@/lib/schedule-utils"

// Hàm chuyển đổi thứ trong tuần
const getDayOfWeek = (date) => {
  const days = ["Chủ nhật", "Thứ 2", "Thứ 3", "Thứ 4", "Thứ 5", "Thứ 6", "Thứ 7"]
  return days[date.getDay()]
}

// Hàm format tháng/năm
const formatMonthYear = (date) => {
  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ]
  return `${months[date.getMonth()]} ${date.getFullYear()}`
}

export default function LichDayPage() {
  const searchParams = useSearchParams()
  const giangVienIdParam = searchParams?.get("giangVienId")

  const [activeTab, setActiveTab] = useState("thoikhoabieu") // "thoikhoabieu" hoặc "lichthi"
  const [selectedDate, setSelectedDate] = useState(new Date())
  const [selectedGiangVien, setSelectedGiangVien] = useState(giangVienIdParam || "Tất cả")
  const [selectedLop, setSelectedLop] = useState("Tất cả")
  const [selectedPhong, setSelectedPhong] = useState("Tất cả")
  
  // State cho modal tạo lịch
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false)
  const [lichDayData, setLichDayData] = useState(initialLichDayData)
  const [formData, setFormData] = useState({
    lopHoc: "",
    batDauTuNgay: null,
    denNgay: null,
    gioBatDau: "09:00",
    gioKetThuc: "10:30",
    coSo: "Cơ sở 1",
    toaNha: "Toàn nhà A",
    phong: "Phòng 205",
  })
  const [conflictWarning, setConflictWarning] = useState(null)

  // Cập nhật selectedGiangVien khi searchParams thay đổi
  useEffect(() => {
    if (giangVienIdParam) {
      setSelectedGiangVien(giangVienIdParam)
    }
  }, [giangVienIdParam])

  // Lấy danh sách lớp học và phòng học từ dữ liệu
  const lopList = useMemo(() => {
    const uniqueLops = [...new Set(lichDayData.map((lich) => lich.lop))]
    return uniqueLops
  }, [])

  const phongList = useMemo(() => {
    const uniquePhongs = [...new Set(lichDayData.map((lich) => lich.phongHoc))]
    return uniquePhongs
  }, [])

  // Lọc lịch dạy theo các tiêu chí
  const filteredLichDay = useMemo(() => {
    return lichDayData.filter((lich) => {
      // Lọc theo giảng viên
      if (selectedGiangVien !== "Tất cả" && lich.giangVienId !== selectedGiangVien) {
        return false
      }

      // Lọc theo lớp
      if (selectedLop !== "Tất cả" && lich.lop !== selectedLop) {
        return false
      }

      // Lọc theo phòng
      if (selectedPhong !== "Tất cả" && lich.phongHoc !== selectedPhong) {
        return false
      }

      // Lọc theo ngày được chọn
      const lichDate = new Date(lich.ngay)
      const selected = new Date(selectedDate)
      if (
        lichDate.getDate() !== selected.getDate() ||
        lichDate.getMonth() !== selected.getMonth() ||
        lichDate.getFullYear() !== selected.getFullYear()
      ) {
        return false
      }

      return true
    })
  }, [selectedDate, selectedGiangVien, selectedLop, selectedPhong])

  // Lấy danh sách ngày có lịch trong tháng
  const datesWithSchedule = useMemo(() => {
    const dates = new Set()
    lichDayData.forEach((lich) => {
      const date = new Date(lich.ngay)
      dates.add(date.toDateString())
    })
    return dates
  }, [lichDayData])

  // Lấy danh sách lớp học từ classesData
  const availableLopList = useMemo(() => {
    return classesData
      .filter((lop) => lop.trangThai === "Đang hoạt động")
      .map((lop) => ({
        value: lop.tenLop,
        label: `${lop.tenLop} - ${lop.mon}`,
        mon: lop.mon,
        giangVien: lop.giangVien,
      }))
  }, [])

  // Format date cho input
  const formatDateForInput = (date) => {
    if (!date) return ""
    const d = new Date(date)
    const month = String(d.getMonth() + 1).padStart(2, "0")
    const day = String(d.getDate()).padStart(2, "0")
    const year = d.getFullYear()
    return `${month}/${day}/${year}`
  }

  // Parse date từ input
  const parseDateFromInput = (dateString) => {
    if (!dateString) return null
    const [month, day, year] = dateString.split("/")
    return new Date(year, month - 1, day)
  }

  // Kiểm tra trùng lịch khi form thay đổi
  useEffect(() => {
    if (!formData.batDauTuNgay || !formData.gioBatDau || !formData.gioKetThuc || !formData.lopHoc) {
      setConflictWarning(null)
      return
    }

    const selectedLop = availableLopList.find((lop) => lop.value === formData.lopHoc)
    if (!selectedLop) {
      setConflictWarning(null)
      return
    }

    // Tìm giảng viên của lớp
    const giangVien = giangVienData.find((gv) => gv.hoTen === selectedLop.giangVien)
    if (!giangVien) {
      setConflictWarning(null)
      return
    }

    // Kiểm tra trùng lịch cho từng ngày trong khoảng thời gian
    const startDate = parseDateFromInput(formData.batDauTuNgay)
    const endDate = parseDateFromInput(formData.denNgay) || startDate

    if (!startDate) return

    // Chuyển đổi giờ sang format 24h
    const parseTime = (timeStr) => {
      const cleanTime = timeStr.replace(/AM|PM/i, "").trim()
      const [hour, min] = cleanTime.split(":")
      const isPM = timeStr.toUpperCase().includes("PM")
      let hour24 = parseInt(hour)
      if (isPM && hour24 !== 12) hour24 += 12
      if (!isPM && hour24 === 12) hour24 = 0
      return { hour: hour24, min: parseInt(min) }
    }

    const startTime = parseTime(formData.gioBatDau)
    const endTime = parseTime(formData.gioKetThuc)

    const gioBatDau24 = `${String(startTime.hour).padStart(2, "0")}:${String(startTime.min).padStart(2, "0")}`
    const gioKetThuc24 = `${String(endTime.hour).padStart(2, "0")}:${String(endTime.min).padStart(2, "0")}`

    // Kiểm tra trùng lịch
    let conflictFound = null
    const currentDate = new Date(startDate)

    while (currentDate <= endDate) {
      const dateString = currentDate.toISOString().split("T")[0]
      const conflict = checkScheduleConflict(giangVien.id, dateString, gioBatDau24, gioKetThuc24)

      if (conflict.isConflict && conflict.conflictingSchedule) {
        conflictFound = conflict.conflictingSchedule
        break
      }

      currentDate.setDate(currentDate.getDate() + 1)
    }

    if (conflictFound) {
      setConflictWarning({
        message: `Lịch này trùng với "${conflictFound.monHoc} - ${conflictFound.lop}" từ ${conflictFound.gioBatDau} đến ${conflictFound.gioKetThuc}.`,
      })
    } else {
      setConflictWarning(null)
    }
  }, [formData.batDauTuNgay, formData.denNgay, formData.gioBatDau, formData.gioKetThuc, formData.lopHoc, availableLopList])

  // Xử lý tạo lịch mới
  const handleCreateSchedule = () => {
    if (!formData.lopHoc || !formData.batDauTuNgay) {
      alert("Vui lòng điền đầy đủ thông tin")
      return
    }

    const selectedLop = availableLopList.find((lop) => lop.value === formData.lopHoc)
    if (!selectedLop) return

    const startDate = parseDateFromInput(formData.batDauTuNgay)
    const endDate = parseDateFromInput(formData.denNgay) || startDate

    if (!startDate) return

    // Chuyển đổi giờ
    const parseTime = (timeStr) => {
      const cleanTime = timeStr.replace(/AM|PM/i, "").trim()
      const [hour, min] = cleanTime.split(":")
      const isPM = timeStr.toUpperCase().includes("PM")
      let hour24 = parseInt(hour)
      if (isPM && hour24 !== 12) hour24 += 12
      if (!isPM && hour24 === 12) hour24 = 0
      return { hour: hour24, min: parseInt(min) }
    }

    const startTime = parseTime(formData.gioBatDau)
    const endTime = parseTime(formData.gioKetThuc)

    const gioBatDau24 = `${String(startTime.hour).padStart(2, "0")}:${String(startTime.min).padStart(2, "0")}`
    const gioKetThuc24 = `${String(endTime.hour).padStart(2, "0")}:${String(endTime.min).padStart(2, "0")}`

    // Tạo lịch cho từng ngày trong khoảng thời gian
    const newSchedules = []
    const currentDate = new Date(startDate)

    while (currentDate <= endDate) {
      const dateString = currentDate.toISOString().split("T")[0]
      const dayOfWeek = getDayOfWeek(currentDate)
      const thoiLuong = ((endTime.hour * 60 + endTime.min) - (startTime.hour * 60 + startTime.min)) / 60

      const newSchedule = {
        id: Date.now() + Math.random(),
        monHoc: selectedLop.mon,
        lop: selectedLop.value,
        ngayDay: dayOfWeek,
        thoiGian: `${gioBatDau24} - ${gioKetThuc24}`,
        phongHoc: `${formData.phong} ${formData.toaNha} ${formData.coSo}`,
        giangVien: selectedLop.giangVien,
        thoiLuong: thoiLuong,
        ngay: dateString,
        gioBatDau: gioBatDau24,
        gioKetThuc: gioKetThuc24,
        giangVienId: giangVienData.find((gv) => gv.hoTen === selectedLop.giangVien)?.id || "",
      }

      newSchedules.push(newSchedule)
      currentDate.setDate(currentDate.getDate() + 1)
    }

    // Thêm vào danh sách
    setLichDayData([...lichDayData, ...newSchedules])

    // Reset form và đóng modal
    setFormData({
      lopHoc: "",
      batDauTuNgay: null,
      denNgay: null,
      gioBatDau: "09:00",
      gioKetThuc: "10:30",
      coSo: "Cơ sở 1",
      toaNha: "Toàn nhà A",
      phong: "Phòng 205",
    })
    setConflictWarning(null)
    setIsCreateModalOpen(false)

    // Cập nhật selectedDate để hiển thị lịch mới
    setSelectedDate(startDate)
  }

  return (
    <main className="flex-1 p-6">
      <h1 className="text-[#4A90D9] text-2xl font-bold mb-6">Quản lí lịch</h1>

      {/* Tabs */}
      <div className="bg-white rounded-xl shadow-sm mb-6">
        <div className="flex items-center border-b border-gray-200">
          <button
            onClick={() => setActiveTab("thoikhoabieu")}
            className={`px-6 py-3 text-sm font-medium transition-colors ${
              activeTab === "thoikhoabieu"
                ? "text-[#4A90D9] border-b-2 border-[#4A90D9]"
                : "text-gray-600 hover:text-gray-800"
            }`}
          >
            Thời khóa biểu
          </button>
          <button
            onClick={() => setActiveTab("lichthi")}
            className={`px-6 py-3 text-sm font-medium transition-colors ${
              activeTab === "lichthi"
                ? "text-[#4A90D9] border-b-2 border-[#4A90D9]"
                : "text-gray-600 hover:text-gray-800"
            }`}
          >
            Lịch Thi
          </button>
          <div className="ml-auto mr-6">
            {activeTab === "thoikhoabieu" ? (
              <button
                onClick={() => setIsCreateModalOpen(true)}
                className="flex items-center gap-2 px-4 py-2 bg-[#4A90D9] text-white rounded-lg text-sm font-medium hover:bg-[#3a7bc8] transition-colors"
              >
                <Plus className="w-4 h-4" />
                Tạo lịch
              </button>
            ) : (
              <button className="flex items-center gap-2 px-4 py-2 bg-[#4A90D9] text-white rounded-lg text-sm font-medium hover:bg-[#3a7bc8] transition-colors">
                <Plus className="w-4 h-4" />
                Tạo lịch thi mới
              </button>
            )}
          </div>
        </div>

        <div className="p-6">
          <div className="grid grid-cols-1 lg:grid-cols-[350px_1fr] gap-6">
            {/* Cột trái: Calendar và Bộ lọc */}
            <div className="space-y-6">
              {/* Calendar */}
              <div className="bg-white border border-gray-200 rounded-lg p-4">
                <Calendar
                  mode="single"
                  selected={selectedDate}
                  onSelect={setSelectedDate}
                  className="w-full"
                  modifiers={{
                    hasSchedule: (date) => datesWithSchedule.has(date.toDateString()),
                  }}
                  modifiersClassNames={{
                    hasSchedule: "!bg-orange-500 !text-white !rounded-full !font-semibold",
                  }}
                />
              </div>

              {/* Bộ lọc */}
              <div className="bg-white border border-gray-200 rounded-lg p-4">
                <div className="flex items-center gap-2 mb-4">
                  <Filter className="w-5 h-5 text-gray-600" />
                  <h3 className="text-sm font-semibold text-gray-800">Bộ lọc</h3>
                </div>
                <div className="space-y-4">
                  {/* Lọc theo giảng viên */}
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-2">Giảng viên</label>
                    <select
                      value={selectedGiangVien}
                      onChange={(e) => setSelectedGiangVien(e.target.value)}
                      className="w-full px-3 py-2 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                    >
                      <option value="Tất cả">Tất cả</option>
                      {giangVienData
                        .filter((gv) => gv.trangThai === "Đang hoạt động")
                        .map((gv) => (
                          <option key={gv.id} value={gv.id}>
                            {gv.hoTen}
                          </option>
                        ))}
                    </select>
                  </div>

                  {/* Lọc theo lớp học */}
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-2">Lớp học</label>
                    <select
                      value={selectedLop}
                      onChange={(e) => setSelectedLop(e.target.value)}
                      className="w-full px-3 py-2 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                    >
                      <option value="Tất cả">Tất cả</option>
                      {lopList.map((lop) => (
                        <option key={lop} value={lop}>
                          {lop}
                        </option>
                      ))}
                    </select>
                  </div>

                  {/* Lọc theo phòng học */}
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-2">Phòng học</label>
                    <select
                      value={selectedPhong}
                      onChange={(e) => setSelectedPhong(e.target.value)}
                      className="w-full px-3 py-2 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                    >
                      <option value="Tất cả">Tất cả</option>
                      {phongList.map((phong) => (
                        <option key={phong} value={phong}>
                          {phong}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>
            </div>

            {/* Cột phải: Danh sách lịch */}
            <div>
              {filteredLichDay.length === 0 ? (
                <div className="bg-white border border-gray-200 rounded-lg p-12 text-center">
                  <p className="text-gray-500">Không có lịch nào trong ngày được chọn.</p>
                </div>
              ) : (
                <div className="space-y-4">
                  {filteredLichDay.map((lich) => {
                    const lichDate = new Date(lich.ngay)
                    const dayOfWeek = getDayOfWeek(lichDate)

                    return (
                      <div
                        key={lich.id}
                        className="bg-white border border-gray-200 rounded-lg p-5 relative"
                      >
                        {/* Badge */}
                        <span className="absolute top-4 right-4 px-2 py-1 bg-[#4A90D9] text-white text-xs font-medium rounded">
                          {activeTab === "thoikhoabieu" ? "Lịch dạy" : "Lịch thi"}
                        </span>

                        {/* Thông tin môn học */}
                        <div className="mb-4">
                          <h3 className="text-lg font-bold text-gray-800 mb-1">{lich.monHoc}</h3>
                          <p className="text-sm text-gray-600">{lich.lop}</p>
                        </div>

                        {/* Chi tiết */}
                        <div className="space-y-3 mb-4">
                          <div className="flex items-center gap-3">
                            <Clock className="w-4 h-4 text-gray-400" />
                            <span className="text-sm text-gray-700">
                              {dayOfWeek}, {lich.thoiGian}
                            </span>
                          </div>
                          <div className="flex items-center gap-3">
                            <MapPin className="w-4 h-4 text-gray-400" />
                            <span className="text-sm text-gray-700">{lich.phongHoc}</span>
                          </div>
                          {activeTab === "thoikhoabieu" ? (
                            <div className="flex items-center gap-3">
                              <User className="w-4 h-4 text-gray-400" />
                              <span className="text-sm text-gray-700">GV.{lich.giangVien}</span>
                            </div>
                          ) : (
                            <div className="flex items-center gap-3">
                              <User className="w-4 h-4 text-gray-400" />
                              <span className="text-sm text-gray-700">
                                Giám thị: {lich.giangVien}
                              </span>
                            </div>
                          )}
                        </div>

                        {/* Nút thao tác */}
                        <div className="flex items-center gap-2 pt-4 border-t border-gray-100">
                          <button className="flex items-center gap-2 px-3 py-1.5 text-sm text-gray-700 hover:bg-gray-100 rounded transition-colors">
                            <Edit className="w-4 h-4" />
                            Sửa
                          </button>
                          <button className="flex items-center gap-2 px-3 py-1.5 text-sm text-red-600 hover:bg-red-50 rounded transition-colors">
                            <Trash2 className="w-4 h-4" />
                            Xóa
                          </button>
                          {activeTab === "lichthi" && (
                            <button className="flex items-center gap-2 px-3 py-1.5 text-sm text-gray-700 hover:bg-gray-100 rounded transition-colors ml-auto">
                              <Upload className="w-4 h-4" />
                              Xuất danh sách phòng thi
                            </button>
                          )}
                        </div>
                      </div>
                    )
                  })}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Modal Tạo Lịch Dạy */}
      <Dialog open={isCreateModalOpen} onOpenChange={setIsCreateModalOpen}>
        <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle className="text-xl font-bold text-gray-800">Tạo Lịch Dạy</DialogTitle>
          </DialogHeader>

          <div className="space-y-4 mt-4">
            {/* Lớp học */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Lớp học</label>
              <select
                value={formData.lopHoc}
                onChange={(e) => setFormData({ ...formData, lopHoc: e.target.value })}
                className="w-full px-4 py-2.5 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
              >
                <option value="">Chọn lớp học</option>
                {availableLopList.map((lop) => (
                  <option key={lop.value} value={lop.value}>
                    {lop.label}
                  </option>
                ))}
              </select>
            </div>

            {/* Ngày bắt đầu và kết thúc */}
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Bắt đầu từ ngày</label>
                <div className="relative">
                  <input
                    type="text"
                    placeholder="mm/dd/yyyy"
                    value={formData.batDauTuNgay || ""}
                    onChange={(e) => setFormData({ ...formData, batDauTuNgay: e.target.value })}
                    className="w-full px-4 py-2.5 pr-10 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                  />
                  <CalendarIcon className="absolute right-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400 pointer-events-none" />
                </div>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Đến ngày</label>
                <div className="relative">
                  <input
                    type="text"
                    placeholder="mm/dd/yyyy"
                    value={formData.denNgay || ""}
                    onChange={(e) => setFormData({ ...formData, denNgay: e.target.value })}
                    className="w-full px-4 py-2.5 pr-10 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                  />
                  <CalendarIcon className="absolute right-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400 pointer-events-none" />
                </div>
              </div>
            </div>

            {/* Giờ bắt đầu và kết thúc */}
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Giờ bắt đầu</label>
                <div className="relative">
                  <input
                    type="text"
                    placeholder="09:00 AM"
                    value={formData.gioBatDau}
                    onChange={(e) => setFormData({ ...formData, gioBatDau: e.target.value })}
                    className="w-full px-4 py-2.5 pr-10 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                  />
                  <Clock className="absolute right-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400 pointer-events-none" />
                </div>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Giờ kết thúc</label>
                <div className="relative">
                  <input
                    type="text"
                    placeholder="10:30 AM"
                    value={formData.gioKetThuc}
                    onChange={(e) => setFormData({ ...formData, gioKetThuc: e.target.value })}
                    className="w-full px-4 py-2.5 pr-10 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                  />
                  <Clock className="absolute right-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400 pointer-events-none" />
                </div>
              </div>
            </div>

            {/* Địa điểm */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Địa điểm</label>
              <div className="grid grid-cols-3 gap-3">
                <select
                  value={formData.coSo}
                  onChange={(e) => setFormData({ ...formData, coSo: e.target.value })}
                  className="px-4 py-2.5 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                >
                  <option>Cơ sở 1</option>
                  <option>Cơ sở 2</option>
                </select>
                <select
                  value={formData.toaNha}
                  onChange={(e) => setFormData({ ...formData, toaNha: e.target.value })}
                  className="px-4 py-2.5 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                >
                  <option>Toàn nhà A</option>
                  <option>Toàn nhà B</option>
                  <option>Toàn nhà C</option>
                </select>
                <select
                  value={formData.phong}
                  onChange={(e) => setFormData({ ...formData, phong: e.target.value })}
                  className="px-4 py-2.5 border border-[#4A90D9] rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
                >
                  <option>Phòng 205</option>
                  <option>Phòng 206</option>
                  <option>Phòng 301</option>
                  <option>Phòng 302</option>
                  <option>Phòng 403</option>
                </select>
              </div>
            </div>

            {/* Cảnh báo trùng lịch */}
            {conflictWarning && (
              <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
                <div className="flex items-start gap-3">
                  <AlertTriangle className="w-5 h-5 text-yellow-600 mt-0.5 flex-shrink-0" />
                  <div>
                    <p className="text-sm font-medium text-yellow-800 mb-1">Phát hiện trùng lịch</p>
                    <p className="text-sm text-yellow-700">{conflictWarning.message}</p>
                  </div>
                </div>
              </div>
            )}

            {/* Nút hành động */}
            <div className="flex items-center justify-end gap-3 pt-4 border-t border-gray-200">
              <button
                onClick={() => {
                  setIsCreateModalOpen(false)
                  setFormData({
                    lopHoc: "",
                    batDauTuNgay: null,
                    denNgay: null,
                    gioBatDau: "09:00",
                    gioKetThuc: "10:30",
                    coSo: "Cơ sở 1",
                    toaNha: "Toàn nhà A",
                    phong: "Phòng 205",
                  })
                  setConflictWarning(null)
                }}
                className="px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 transition-colors"
              >
                Huỷ
              </button>
              <button
                onClick={handleCreateSchedule}
                className="px-4 py-2 bg-[#4A90D9] text-white rounded-lg text-sm font-medium hover:bg-[#3a7bc8] transition-colors"
              >
                Lưu Lịch
              </button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </main>
  )
}

